<template>
  <div class="user" id="abc"> 
		<hr>
  <h3>披萨分类</h3> 
  	<div class="productVariety">
	    <router-link class="headerstyle" :to="{path:'/product/'+item.tip+'/'+item.id,query:{info:'关注有礼品'}}" :key="index" v-for="item,index in productList" event="mouseover">
	    <li>{{item.productName}}</li>
	    </router-link> 
  	</div>  
    <div class="product-info" v-if="productInfo.productName">
    	<h4>产品：</h4>{{productInfo.productName}}<p></p>
    	<h4>介绍：</h4>{{productInfo.detail}}
    </div>
		<div v-if="productInfo.productName">
			<router-link class="followshareTop" exact :to="{path:'',query:{info:'关注有礼品'}}"><strong>关注</strong></router-link>
			<router-link class="followshareTop" id="shareTop" exact :to="{path:'',query:{info:'分享有惊喜'}}"><strong>分享</strong></router-link>		
			<div class="follow-share">		
				{{$route.query.info}}
			</div>
		</div>	
  </div>
</template>

<script>
let data = [
	{
		id:1,
		tip:'hot',
		productName:"榴莲披萨",
		detail:'来自新加坡的黄金榴莲，浓浓的榴莲香味，口感不腻，榴莲入口香滑，而且还比较甜，只要品尝一次就一定会爱上这种披萨。'
	},
	{
		id:2,
		tip:'common',
		productName:"培根披萨",
		detail:'这是一款特别大众的披萨，我们应该大多数人都吃过，对于中国人来说，可能是最能接受的味道吧。'
	},
	{
		id:3,
		tip:'hot',
		productName:"海鲜披萨",
		detail:'如果你喜欢吃海鲜，而又觉得海鲜太贵的话，那么就吃这款披萨吧，红色的虾仁与青椒搭配，颜色特别好看，也特别诱人，关键是海鲜味特别鲜美。'
	},
	{
		id:4,
		tip:'common',
		productName:"水果披萨",
		detail:'如果你喜欢吃水果，而只吃水果又不饱的话，那么就选这一款披萨吧里面各种各样的水果，只要你喜欢，香蕉、火龙果、提子...任你挑选'
	},	
	];
export default {
  data(){
    return {
      productList:data,
      productInfo:{
      }
    }
  },
  watch:{
    $route(){
        // 路径发生变化，$route会重新赋值，监控了这个属性，会执行这个函数
      this.getData()
    }
  },
  created(){
		this.$emit('header', false);
		this.getData()		   
  },
  methods:{
    getData(){  	
	  	let id=this.$route.params.productId;
	  	if(id){
	  		this.productInfo=this.productList.filter((item)=>{
	  			return item.id==id
	  		})[0]
	  	}else{
	  		this.productInfo={};
		}
	}		  	
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.productVariety{
	width:30%;
	height:450px;
	display:inline-block;
	float:left;
	border-top:1px solid #ccc;
	border-right:1px solid #ccc;
	border-radius:5px 5px 0 0;
}

.headerstyle{
	width:100%;
	height:60px;
	display:block;
	line-height:60px;
}
.headerstyle li{
	border-bottom:1px solid #ccc;
	border-radius:5px;
	width:100%;
}

.product-info{
	width:60%;
	color:black;
	float:right;
	text-align:left;
	//text-indent:1em;
	margin-right:20px;
}

.product-info p{
	margin:0;
	height:50px;
	line-height:50px;
}
p,h4{
	margin:0;
	padding:0;
}
h3{
	width:30%;
}

.followshareTop{
	display:inline-block;
	width:60px;
	height:40px;	
	line-height: 40px;
	text-align: center;
	font-size: 18px;
	position:fixed;
	bottom:130px;
	right:10px;
}
#shareTop{
	bottom:90px;
}

.follow-share{
	width:120px;
	left:0;
	right:0;
	margin:auto;
	text-align: center;
	position:fixed;
	color:red;
	bottom:100px;
}
</style>


